@import url(../../node_modules/normalize.css/normalize.css);
* {
  box-sizing: border-box; }

body {
  font-family: "Helvetica Neue",Helvetica,"Microsoft Yahei",Arial,sans-serif;
  font-size: 16px;
  color: #333; }

input {
  outline: 0; }

.textbox {
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 7px 10px;
  display: inline-block;
  width: 100%;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); }
  .textbox:focus {
    outline: 0; }

.hidden {
  display: none; }

.container {
  margin: 0 auto;
  padding: 0 15px; }
  @media screen and (min-width: 768px) {
    .container {
      width: 750px; } }
  @media screen and (min-width: 992px) {
    .container {
      width: 970px; } }
  @media screen and (min-width: 1200px) {
    .container {
      width: 1170px; } }

.pull-right {
  float: right !important; }

.markdown pre {
  background-color: #eee;
  color: #555;
  border-radius: 3px;
  padding: 10px;
  max-height: 500px;
  overflow: auto;
  border: none;
  font-size: 0.9em;
  line-height: 1.33; }
  .markdown pre code {
    font-size: inherit;
    color: inherit;
    padding: 0;
    background-color: transparent; }

.markdown code {
  color: #c7254e;
  background-color: #eee;
  font-size: 0.9em;
  padding: 2px 4px; }

.markdown blockquote {
  margin: 0;
  padding: 5px 10px 5px 15px;
  border-left: 5px solid steelblue;
  background-color: #eee; }

.markdown table {
  border: 1px solid #ccc; }
  .markdown table thead tr {
    background-color: #eee; }
  .markdown table tbody tr {
    border-top: 1px solid #ccc; }
    .markdown table tbody tr:nth-of-type(odd) {
      background-color: #fff; }
    .markdown table tbody tr:nth-of-type(even) {
      background-color: #eee; }
  .markdown table th, .markdown table td {
    padding: 8px;
    border-left: 1px solid #ccc; }

.markdown hr {
  border-style: solid;
  border-color: #ccc; }

#editor {
  margin-top: 20px;
  width: 100%;
  max-width: 1600px; }

.editor-title {
  font-size: 18px; }

.editor-title .textbox,
.md-editor textarea,
.md-preview,
.tag-input input {
  line-height: 1.33; }

/* Style for Tag Input */
.tag {
  display: inline-block;
  padding: 3px 8px;
  margin-right: 5px;
  color: steelblue;
  background-color: lightblue;
  border-radius: 3px;
  font-size: .8em;
  font-weight: 300; }

.tag-input {
  position: relative;
  margin: 20px 0;
  cursor: text; }
  .tag-input input {
    border: none;
    padding: 2px 0;
    margin-left: 3px;
    font-size: .8em; }
    .tag-input input:focus {
      outline: 0; }
  .tag-input .hidden {
    position: absolute;
    opacity: 0;
    left: 0; }

/* Style for Editor */
.md-panel {
  display: block;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 14px;
  margin-bottom: 20px; }

.md-toolbar {
  position: absolute;
  width: 100%;
  height: 37px;
  margin: 0;
  padding: 0 5px;
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 1px #ccc;
  z-index: 10; }

.tb-btn {
  float: left;
  font-size: 16px;
  color: #aaa;
  list-style: none;
  margin: 0 2px; }
  .tb-btn.spliter {
    border-left: 1px solid #ccc;
    margin: 6px 2px;
    height: 24px; }
  .tb-btn a {
    display: inline-block;
    width: 36px;
    text-align: center;
    padding: 9px 0;
    cursor: pointer; }
    .tb-btn a:hover {
      color: #0081FF;
      background-color: #eee; }
    .tb-btn a:active {
      box-shadow: inset 0 1px 5px rgba(0, 0, 0, 0.075); }

.md-editor {
  width: 50%;
  height: auto; }

.md-editor textarea {
  border-style: none;
  resize: none;
  outline: 0;
  height: 100%;
  min-height: 540px;
  width: 100%;
  margin-top: 47px;
  padding: 0 15px; }

.md-preview {
  position: absolute;
  width: 50%;
  height: 100%;
  left: 50%;
  top: 0;
  background-color: #f6f6f6;
  padding: 45px 20px 20px 20px;
  overflow: auto; }

.md-spliter {
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  border-left: 1px solid #ccc;
  z-index: 105; }

.md-resizer {
  position: absolute;
  width: 50px;
  height: 4px;
  left: 50%;
  margin-left: -25px;
  bottom: -7px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  cursor: row-resize; }
